img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif, "Arial";
  font-size: 14px;
  line-height: 1.2; }

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/inter/inter-400.woff2") format("woff2"), url("../fonts/inter/inter-400.woff") format("woff"); }

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/inter/inter-700.woff2") format("woff2"), url("../fonts/inter/inter-700.woff") format("woff"); }

.page {
  height: 100%;
  background-color: #ffffff; }

.page__wrapper {
  min-height: 100%;
  max-width: 1220px;
  min-width: 320px;
  margin: 0 auto;
