.header {
  margin-bottom: 100px; }

.header__inner {
  display: flex;
  justify-content: space-between; }

.menu__list {
  list-style: none;
  margin: 0 60px 0 0;
  padding: 0;
  display: flex; }

.menu__item:not(:last-child) {
  margin-right: 10px; }

.menu__link {
  display: block;
  text-decoration: none;
  color: inherit;
  padding: 41px 20px;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 17px;
  transition: all 0.3s ease-in-out; }
