.bike {
  margin-bottom: 50px; }

.bike__inner {
  display: flex;
  justify-content: space-between; }

.bike__column_slider {
  flex-basis: 630px;
  max-width: 630px; }

.bike__column_description {
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }

.title {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 64px;
  line-height: 77px; }

.bike__subtitle {
  margin-bottom: 60px;
  font-size: 32px;
