.advantages {
  padding-top: 100px;
  margin-bottom: 50px; }

.advantages__inner {
  max-width: 1120px;
  display: flex;
  gap: 60px; }

.advantages__list {
  list-style: none; }

.advantages__item:not(:last-child) {
  margin-bottom: 50px; }

.advantages__title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase; }

.advantages__description {
  max-width: 450px; }

.specifications {
  padding-top: 100px;
  margin-bottom: 150px; }

.specifications__inner {
  display: flex;
  gap: 60px; }

.specifications__title {
  margin-bottom: 40px;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase; }

.specifications__image {
  align-self: center; }

.specifications__list {
  max-width: 570px;
  list-style: none; }

.specifications__item:not(:last-child) {
  margin-bottom: 32px; }

.specifications__name {
  padding: 3px 0 2px 4px;
  margin-bottom: 10px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #131313; }

.specifications__discr {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 5px; }
  .specifications__discr dt {
    grid-column: 1;
    padding-right: 10px; }
  .specifications__discr dd {
    grid-column: 2; }

.gallery {
  margin-bottom: 50px; }

.gallery__list {
  list-style: none;
  max-width: 1200px; }

.gallery__item {
  max-height: 604px;
  padding-left: 50px; }

.gallery__image {
  vertical-align: top;
  object-fit: cover; }

.price {
  margin-bottom: 100px; }

.price_inner {
  position: relative; }

.form {
  max-width: 420px; }

.form__title {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 40px; }

.form__description {
  max-width: 300px;
  margin-bottom: 30px; }

.input__name,
.input__tel {
  display: block;
  width: 100%;
  padding: 10px 0 10px 6px;
  margin-bottom: 20px;
  border: none;
  border-bottom: 1px solid #131313; }
  .input__name::placeholder,
  .input__tel::placeholder {
    font-family: inherit;
    color: #6f6f6f;
    opacity: 0.5; }

.form__color {
  margin-bottom: 10px;
  color: #6f6f6f;
  opacity: 0.5; }

.form__radio-container {
  display: flex; }

.form__label {
  margin-bottom: 50px; }
  .form__label:not(:last-child) {
    margin-right: 23px; }

.card {
  display: none;
  position: absolute;
  right: 0;
  bottom: 0; }

.card__image {
  vertical-align: top; }

.card__price {
  position: absolute;
  top: 28px;
  left: 0;
  width: 221px;
  height: 100px;
  transform: translateX(-50%);
  border: 1px solid #ff4040;
  border-radius: 50%;
  text-transform: uppercase;
  font-size: 32px;
  line-height: 100px;
  text-align: center; }
  .card__price::before {
    position: absolute;
    content: "";
    top: 12px;
    left: 0;
    width: 221px;
    height: 100px;
    border: 1px solid #ff4040;
    border-radius: 50%; }

.card__btn {
  width: 80px;
  height: 12px;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.2s ease-in; }
  .card__btn:hover {
    transform: scale(1.04); }
  .card__btn_grey {
    background-color: #6f6f6f; }
  .card__btn_white {
    background-color: #ffffff;
    border: 1px solid #6f6f6f; }
  .card__btn_red {
    background-color: #ff4040; }

.card__btn:checked + .card {
  display: block; }

.form__input-radio:checked + .card {
  display: block; }

.footer {
  background-color: #131313; }

.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100px;
  max-width: 1200px;
  margin: 0 auto; }

.footer__conf,
.social__link,
.contacts__link {
  color: #ffffff;
  text-decoration: none;
  text-transform: lowercase;
  transition: all 0.3s ease-in-out;
  font-size: 12px; }
  .footer__conf:hover,
  .social__link:hover,
  .contacts__link:hover {
